.main-content-wrapper{
    width: calc(100% - 4em);
    margin-left: 4em;
    height: 100%;
}
.special{
    color: #7ad71f;
}
.insert-it{
    position: relative;
    left: -2em;
    -webkit-animation-name: insertion;
            animation-name: insertion;
    transition-timing-function: ease-in;
    -webkit-animation-duration: 0.75s;
            animation-duration: 0.75s;
    -webkit-transform: translateX(2em);
            transform: translateX(2em);
}
.react-rotating-text-cursor {
    -webkit-animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s infinite;
            animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s infinite;
  }
  .rc-anchor-dark.rc-anchor-normal{
    border-color: #7ad71f !important;
    
  }
  @-webkit-keyframes blinking-cursor {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  @keyframes blinking-cursor {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
@-webkit-keyframes insertion {
    from{-webkit-transform: translateX(0);transform: translateX(0)}
    to{
        -webkit-transform: translateX(2em);
                transform: translateX(2em)
    }
}
@keyframes insertion {
    from{-webkit-transform: translateX(0);transform: translateX(0)}
    to{
        -webkit-transform: translateX(2em);
                transform: translateX(2em)
    }
}
.flex{
    display: flex;
}
.center-it{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
